<h3> Dialog</h3>
<p> Dialog  Dialog append-to-body </p>
<div class="demo-block">
    <xc-button type="text" @click="outerVisible = true"> Dialog</xc-button>
    <xc-dialog title=" Dialog" :visible.sync="outerVisible">
        <xc-button keyboard="1"></xc-button>
        <xc-button keyboard="2"></xc-button>
        <xc-button keyboard="3"></xc-button>
        <xc-button keyboard="4"></xc-button>
        <xc-dialog title=" Dialog" append-to-body="" :visible.sync="innerVisible">
                <xc-button keyboard="1"></xc-button>
                <xc-button keyboard="2"></xc-button>
        </xc-dialog>
        <span slot="footer" class="dialog-footer">
            <xc-button @click="outerVisible = false"> </xc-button>
            <xc-button type="primary" @click="innerVisible = true"> Dialog</xc-button>
        </span>
    </xc-dialog>
</div>
