<h3></h3>
<p>Dialog Form </p>
<div class="demo-block">
    <xc-button type="text" @click="formVisible = true"> Dialog</xc-button>
    <xc-dialog title=" Dialog" append-to-body="" :visible.sync="formVisible">
            <xc-form ref="ruleForm" :model="form" :rules="rules" label-width="100px">
                <xc-form-item label="" prop="name">
                    <xc-input v-model="form.name" placeholder=""></xc-input>
                </xc-form-item>
                <xc-row :gutter="24">
                    <xc-col :span="14">
                        <xc-form-item label="">
                            <xc-input v-model="form.fundName" placeholder=""></xc-input>
                        </xc-form-item>
                    </xc-col>
                    <xc-col :span="10">
                        <xc-form-item label="" label-width="50px">
                            <xc-input v-model="form.desc" placeholder=""></xc-input>
                        </xc-form-item>
                    </xc-col>
                </xc-row>
                <xc-form-item label="">
                    <xc-input v-model="form.file" readonly placeholder="">
                        <xc-button slot="append" type="warning"></xc-button>
                    </xc-input>
                </xc-form-item>
                <xc-form-item label="">
                    <xc-radio-group v-model="form.type">
                        <xc-radio label="a"></xc-radio>
                        <xc-radio label="b"></xc-radio>
                    </xc-radio-group>
                </xc-form-item>
                <xc-form-item label="">
                    <xc-checkbox-group v-model="form.fund">
                        <xc-checkbox label=""></xc-checkbox>
                        <xc-checkbox label=""></xc-checkbox>
                        <xc-checkbox label=""></xc-checkbox>
                        <xc-checkbox label=""></xc-checkbox>
                        <xc-checkbox label=""></xc-checkbox>
                        <xc-checkbox label=""></xc-checkbox>
                        <xc-checkbox label="ST"></xc-checkbox>
                    </xc-checkbox-group>
                </xc-form-item>
                <xc-form-item>
                    <xc-button type="primary" @click="submitForm('ruleForm')"></xc-button>
                </xc-form-item>
            </xc-form>
    </xc-dialog>
</div>
