<script>
  export default {
    methods: {
      openLoading_1() {
        this.$loading.show({
          text: 'hhh',
        });
        setTimeout(() => {
          this.$loading.hide();
        }, 1000)
      },
      openLoading_2() {
        this.$loading.show();
        setTimeout(() => {
          this.$loading.hide();
        }, 1000)
      },
      openLoading_3() {
        this.$loading.show('');
        setTimeout(() => {
          this.$loading.hide();
        }, 1000)
      },
      openLoading_4() {
        this.$loading.show({
          text: '',
          onShow() {
            console.log('');
          },
          onHide() {
            console.log('');
          }
        });
        setTimeout(() => {
          this.$loading.hide();
        }, 1000)
      }
    }
  }
</script>


