<script>
    export default {
        data() {
            return {
                form: {
                    name: '',
                    fundName: '',
                    file: '',
                    type: 'a',
                    desc: '',
                    fund: []
                },
                rules: {
                    name: [
                        { required: true, message: '', trigger: 'blur' },
                        { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
                    ],
                    fundName: [
                        { required: true, message: '', trigger: 'blur' },
                    ]
                }
            }
        },

        watch: {
            form: {
                handler: function (val, oldVal) { 
                    //console.log(JSON.stringify(val))
                },
                deep: true
            }
        },

        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        alert('submit!');
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            }
        }
    };
</script>


